.portfolio {
/* Слайдер */
	.slick-list {
		overflow: hidden;
	}

	/* Лента слайдов */
	.slick-track {
		display: flex;
		gap: 35px;

		@media (max-width: 768px) {
			gap: 0;
		}
	}

	/* Слайд */
	.slick-slide {
		display: flex;
		flex-direction: column;
		min-height: 100%;
	}

	/* Точки (булиты) */
	.slick-dots {
		position: absolute;
		bottom: 56px;
		display: flex;
		justify-content: center;
		align-self: center;
		background-color: transparent;
	}

	.slick-dots li{
		list-style: none;
		margin: 0 11px;
	}

	/* Активная точка */
	.slick-dots li.slick-active button{
		opacity: .9;
	}

	/* Элемент точки */
	.slick-dots li button {
		width: 70px;
		height: 8px;
		border-radius: 3px;
		border: none;
		font-size: 0;
		background-color: #fff;
		opacity: .3;
		pointer-events: none;
	}
}
