.footer {
	padding: 80px 0;
	text-align: center;
	color: #fff;

	background-image: url("../img/004.jpg");
	background-size: cover;
	background-repeat: no-repeat;
	background-position: bottom -600px center;

	@media (max-width: 1424px) {
		background-position: bottom center;
	}
}

.footer__text {
	position: relative;
	width: 60%;
	margin: 0 auto;
	text-align: center;
	font-size: 20px;
	line-height: 120%;
	font-weight: 700;
	margin-bottom: 250px;

	&::before {
		@include pseudo;

		top: -30px;
		left: 10%;
		width: 80%;
		height: 2px;
		background-color: #fff;
	}

	@media (max-width: 767px) {
		width: 100%;
	}
}

.footer__links {
	@include list-style;

	display: flex;
	gap: 30px;
	flex-direction: column;
	align-items: center;
}