.hero {
	display: flex;
	width: 100%;
	position: relative;
	padding-bottom: 382px;

	background-image: url("../img/001.jpg");
	background-size: contain;
	background-repeat: no-repeat;
	background-position: top center;

	@media (max-width: 1024px) {
		background-position: top -200px center;
		background-size: cover;
		padding-bottom: 80px;
	}

	&--impressum {
		height: 800px;
		background-image: url("../img/009.jpg");
		background-size: cover;
		background-repeat: no-repeat;
		background-position: top -200px center;

		@media (max-width: 1024px) {
			height: 650px;
			background-size: contain;
			background-position: top center;
			padding-top: 150px;
		}
	}
}

.hero__logo {
	position: relative;
	display: flex;
	max-width: 760px;
	margin: 0 auto;
	flex-direction: column;
	align-items: center;
	margin-top: 84px;
	margin-bottom: 382px;

	@media (max-width: 1024px) {
		margin-top: 40px;
		margin-bottom: 450px;
	}

	&--impressum {
		margin: 50px 0 0 auto;
		max-width: 436px;
		
		@media (max-width: 1024px) {
			margin: 0 auto;
		}
	}

	&::before {
		@include pseudo;
		bottom: 75px;
		left: 0;
		height: 3px;
		width: 100%;
		background-color: #fff;

		@media (max-width: 1024px) {
			bottom: -50px;
		}
	}

	& span {
		margin-top: 75px;
		font-size: 36px;
		font-style: normal;
		font-weight: 300;
		line-height: 49px;

		@media (max-width: 1024px) {
			display: none;
		}
	}

	&--impressum {
		&::before {
			bottom: 130px;
		}

		& span {
			text-align: center;
			
			@media (max-width: 1024px) {
				display: inline-block;
			}
		}
	}
}