.contact__caption {
	position: relative;
	z-index: 1;
	text-align: center;
	font-size: 40px;
	font-weight: 700;
	line-height: 52px;
	// margin-top: 300px;
}

.contact__form {
	width: 100%;	
	padding: 25px 35px 100px 35px;
	border-radius: 1rem;
	display: flex;
	flex-direction: column;
	gap: 4rem;
	background-color: #0F3920;

	& label {
		font-size: 20px;
		font-weight: 900;
		line-height: 28px;
	}

	@media (max-width: 900px) {
		gap: 50px;
	}
}

.contact__form-caption {
	text-align: center;
	font-size: 20px;
	font-weight: 900;
	line-height: 28px; 
}

.contact__radio {
	flex-shrink: 0;
	width: 30px;
	height: 30px;
	background-color: #D9D9D9;
	margin-right: 30px;

	@media (max-width: 500px) {
		align-self: start;
	}
}

.contact__radio-wrapper {
	display: flex;
	gap: 1rem;
	align-items: center;

	@media (max-width: 500px) {
		flex-direction: column;
	}
}

.contact__short-input {
	margin-top: 1rem;
	width: 50px;
	border: none;	
	outline: none;
	height: 54px;
	background-color: #D9D9D9;
}

.contact__form-input, textarea {
	border: none;	
	outline: none;
	height: 54px;
	background-color: #D9D9D9;
	margin-top: 1rem;
}

textarea {
	height: 106px;
}

.contact__form-line {
	display: flex;
	width: 100%;
	gap: 80px;

	@media (max-width: 900px) {
		flex-direction: column;
		gap: 50px;
	}
}

.contact__form-field {
	display: flex;
	width: 100%;
	flex-direction: column;
}