.header__body {
	z-index: 1;
	position: relative;
	justify-content: space-between;
	display: flex;
	align-items: center;
	padding: 20px 0;
}

.header__nav {
	position: fixed;
	z-index: 99;
	top: -100%;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #000;
	padding-top: 100px;
	overflow: auto;
	transition: all 0.5s ease;

	&.active {
		top: 0;
	}

	@media (min-width: 768px) {
		display: flex;
		flex-wrap: wrap;
		overflow: unset;
		padding-top: 0;
		top: 0;
		height: auto;
		width: auto;
		position: relative;
		z-index: 3;
		background-color: transparent;
	}
}

.header__nav-list {
	display: flex;
	gap: 60px;
	flex-direction: column;
	padding: 0;
	// margin-left: 20px;

	@media (min-width: 768px) {
		flex-direction: row;
	}
}

.header__nav-item {
	list-style: none;
	margin-bottom: 20px;
	text-transform: uppercase;
	font-size: 20px;
	color: #fff;
	font-weight: 700;
	width: 80%;
	margin-left: auto;

	@media (min-width: 768px) {
		width: auto;
		margin-left: 0;
		position: relative;
		padding-bottom: 5px;
		margin-bottom: 0;
		font-size: 14px;
		font-weight: 500;

		&::before {
			@include pseudo;
			bottom: 0;
			left: 0;
			width: 0;
			height: 1px;
			background-color: #fff;
			transition: all 0.5s ease;
		}

		&:hover::before {
			width: 100%;
		}
	}
}

.header__nav-link {
	@media (max-width: 767px) {
		width: 100%;
		display: inline-block;
		background-color: #c3aba2f0;
		padding: 15px;
		border-top-left-radius: 5px;
		border-bottom-left-radius: 5px;
	}

	@media (min-width: 768px) {
		background-color: transparent;
	}
}

.header__menu {
	display: block;
	position: relative;
	width: 30px;
	height: 22px;
	position: relative;
	z-index: 100;

	&::before,
	&::after {
		@include pseudo;
		background-color: #fff;
		position: absolute;
		width: 100%;
		height: 2px;
		left: 0;

		transition: all 0.5s ease;
	}

	&::before {
		top: 0;
	}

	&::after {
		bottom: 0;
	}

	& span {
		position: absolute;
		left: 0;
		top: 10px;
		width: 100%;
		height: 2px;
		background-color: #fff;

		transition: all 0.5s ease;
	}

	&.active::before {
		transform: rotate(45deg);
		top: 11px;
	}

	&.active::after {
		transform: rotate(-45deg);
		bottom: 9px;
	}

	&.active span {
		transform: scale(0);
	}

	@media (min-width: 768px) {
		display: none;
	}
}