.btn {
	display: flex;
	min-height: 56px;
	padding: 16px 65px;
	border: 1px solid #FFF;
	background-color: transparent;

	justify-content: center;
	align-items: center;
	text-align: center;

	color: #FFF;
	font-size: 20px;
	font-weight: 900;
	line-height: 28px; 

	cursor: pointer;
	transition: border-radius .3s;

	&:hover {
		border-radius: 8px;
	}
}