.section--alvarez {
	margin-top: 500px;
	min-height: 1200px;
	padding: 130px 0;
	background-image: url("../img/003.jpg");
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;

	@media (max-width: 1024px) {
		margin-top: 200px;
		min-height: auto;
	}
}

.section--contact {
	position: relative;
	padding: 130px 0;
	background-image: url("../img/008.jpg");
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;

	&::before {
		@include pseudo;
		top: 0;
		left: 0;
		height: 500px;
		width: 100%;
		background: linear-gradient(180deg, rgba(0,0,0,1) 12%, rgba(255,255,255,0) 65%);
	}
}

.section--alvarez-second {
	padding-top: 120px;
}

.section__header {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0 auto;

	&--alvarez {
		margin-top: -200px;
		margin-bottom: 150px;
	}
}

.section__caption {
	font-size: 40px;
	font-style: normal;
	font-weight: 700;
	line-height: 130%;
	margin-bottom: 80px;

	@media (max-width: 1024px) {
		font-size: 30px;
	}
}

.section__name {
	margin-top: 1rem;
	font-size: 67px;
	font-style: normal;
	font-weight: 500;
	line-height: normal;
	text-transform: uppercase;
}

.section__text {
	font-size: 36px;
	font-style: normal;
	font-weight: 300;
	line-height: 130%;

	@media (max-width: 1024px) {
		font-size: 27px;

		&--alvarez {
			text-align: center;
		}
	}
}

.section__content {
	width: 68%;
	margin: 0 auto;
	position: relative;

	@media (max-width: 1024px) {
		width: 100%;
	}

	&--full {
		width: 100%;
	}
}

.section__content-wrapper {
	display: flex;
	width: 100%;
	gap: 50px;
	margin-bottom: 50px;

	& p {
		width: 50%;
		text-align: right;
		font-size: 20px;
		font-weight: 400;
		line-height: 28px;
	}

	&--revers {
		& p {
			text-align: left;
		}
	}


	& img {
		width: 50%;
		object-fit: cover;
	}

	@media (max-width: 767px) {
		flex-direction: column-reverse;

		&--revers {
			flex-direction: column;
		}

		& p {
			width: 100%;
			text-align: left;
		}

		& img {
			height: 400px;
			flex-shrink: 0;
			max-width: unset;
			margin-left: -20px;
			width: calc(100% + 40px);
		}
	}
}