*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	// scroll-behavior: smooth;
}

.content-box-component {
  box-sizing: content-box;
}

body {
	background-color: #000;
	position: relative;
	height: 100%;
	margin: 0 auto;
	color: #FFF;
	font-family: 'Inter', sans-serif;
	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	line-height: 18px; /* 100% */
}

a {
	text-decoration: none;
	color: inherit;
}

ul,
ol,
li {
	padding: 0;
	margin: 0;
}

img {
	max-width: 100%;
	height: auto;
	vertical-align: top;
}

.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	overflow: hidden;
}