.portfolio {
	padding: 262px 0;
	background-image: url("../img/002.jpg");
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: bottom -300px right;

	@media (max-width: 1024px) {
		padding: 50px 0;
		margin-bottom: 50px;
	}
}

.portfolio__caption {
	font-size: 20px;
	font-weight: 900;
	margin-bottom: 34px;
	padding: 20px
}

.portfolio__description {
	margin: 0;
	font-size: 20px;
	font-weight: 400;
	line-height: 28px; 	
	padding: 20px;
}

.portfolio__list {
	@include list-style;

	display: flex;
	width: 100%;
	gap: 40px;
	overflow: hidden;
}

.portfolio__item {
	cursor: grab;
	background-color: #000;

	@media (max-width: 768px) {
		flex-shrink: 0;
		width: 100%;
	}
}
.portfolio__prev {
	margin-bottom: -110px;

	& img {
		width: 100%;
		height: 406px;
		object-fit: cover;
	}
}